<template>
  <div id="school-name">
    <p>{{school_object.SCHUL_NM}}</p>
  </div>
  <div>
    <div class="schedule" v-if="loaded">
      <table class="type01">
        <tr v-for="i in 7" :key="i">
          <th scope="row">{{i}}교시</th>
          <td v-for="j in 5" :key="j">{{ school_schedule[j-1][i-1] }}</td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import cookiemanager from "@/assets/js/cookiemanager";
import neismanager from "@/assets/js/neismanager";
import timemanager from "@/assets/js/timemanager";

export default {
  name: "SchoolSchedule",
  data () {
    return {
      school_object: null,
      loaded: false,
      school_schedule: this.createArray(5,7)
    }
  },
  created() {
    for(let i=0;i<5;i++) {
      for(let j=0;j<7;j++) {
        this.school_schedule[i][j] = '정보가 없음니다'
      }
    }
    this.school_object = JSON.parse(cookiemanager.getCookie('school_object'))
    neismanager.getMiddleSchoolSchedule(this.school_object, cookiemanager.getCookie('school_grade'), cookiemanager.getCookie('school_class'), timemanager.getCurrentWeek()[0].date ,(sc) => {
      sc.forEach(s => {
        let weekday = timemanager.stringToDate(s['ALL_TI_YMD']).getDay()-1
        let time = parseInt(s['PERIO'])-1
        this.school_schedule[weekday][time] = s['ITRT_CNTNT'].replace("-","")
      })
      console.log(this.school_schedule[1][1])
      this.loaded = true

    })
  },
  methods: {
    getCurrentWeek: function () {
      return timemanager.getCurrentWeek()
    },
    createArray: function (rows, columns) {
      var arr = new Array(rows);
      for (var i = 0; i < rows; i++) {
        arr[i] = new Array(columns);
      }
      return arr;
    }
  }
}
</script>

<style scoped>
.schedule {
  align-content: center;
}
.day{
  float: left;
  margin-left: 1%;
}
</style>